import { request } from 'utils/index';

const prefix = '/api/students';

export const getStudents = ({
  classId,
  classname,
  studentCode,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Student',
    method: 'GET',
    params: {
      classId,
      classname,
      studentCode,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const searchStudents = ({
  studyFieldId,
  fullname,
  studentCode,
  trainTypeId,
  facultyId,
  courseId,
  classId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/Student/Search',
    method: 'GET',
    params: {
      studyFieldId,
      fullname,
      studentCode,
      trainTypeId,
      facultyId,
      courseId,
      classId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getStudentByCode = studentCode =>
  request({
    prefix,
    url: `/Student/GetByCode/${studentCode}`,
    method: 'GET',
  });

export const getStudentById = ({ id } = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const deleteStudent = id =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });

export const updateStudent = ({
  id,
  candidateId,
  code,
  firstName,
  lastName,
  idCard,
  placeIssue,
  dateIssue,
  pobId,
  dob,
  gender,
  homeVillage,
  nationId,
  ethnicityId,
  religion,
  comeFromId,
  unionDate,
  politicalDate,
  permanentAddress,
  wardId,
  townId,
  districtId,
  policyObjectId,
  subsidizeObjectId,
  prioritizeObjectId,
  homePhone,
  phone,
  email,
  mailAddress,
  emergencyAddress,
  estimatedNote,
  foreignLanguage,
  liveAddress,
} = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'PUT',
    params: {},
    data: {
      candidateId,
      code,
      firstName,
      lastName,
      idCard,
      placeIssue,
      dateIssue,
      pobId,
      dob,
      gender,
      homeVillage,
      nationId,
      ethnicityId,
      religion,
      comeFromId,
      unionDate,
      politicalDate,
      permanentAddress,
      wardId,
      townId,
      districtId,
      policyObjectId,
      subsidizeObjectId,
      prioritizeObjectId,
      homePhone,
      phone,
      email,
      mailAddress,
      emergencyAddress,
      estimatedNote,
      foreignLanguage,
      liveAddress,
    },
  });

export const updateStudentUploadAvatar = ({ id, data } = {}) =>
  request({
    prefix,
    url: `/Student/${id}/UploadAvatar`,
    method: 'PUT',
    params: {},
    data,
  });

export const getTrainingScoresByStudentId = async id =>
  request({
    prefix,
    url: '/Student/GetTrainingScoresByStudentId',
    method: 'GET',
    params: { studentId: id },
    data: {},
  });

export const getHistoricalReceiptsByStudentId = async id =>
  request({
    prefix: '/api/finance',
    url: '/Receipt/GetHistoricalReceiptsByStudentId',
    method: 'GET',
    params: { studentId: id },
    data: {},
  });

export const getFamily = ({
  studentId = 1,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/Family',
    method: 'GET',
    params: { studentId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getRelationshipTypes = () =>
  request({
    prefix,
    url: '/Family/RelationshipTypes',
    method: 'GET',
    params: {},
    data: {},
  });

export const createRelationship = ({
  studentId,
  fullname,
  dob,
  homeAddress,
  career,
  relationshipTypeId,
  phoneNo,
}) =>
  request({
    prefix,
    url: '/Family',
    method: 'POST',
    params: {},
    data: { studentId, fullname, dob, homeAddress, career, relationshipTypeId, phoneNo },
  });

export const updateRelationship = ({
  id,
  studentId,
  fullname,
  dob,
  homeAddress,
  career,
  relationshipTypeId,
  phoneNo,
}) =>
  request({
    prefix,
    url: `/Family/${id}`,
    method: 'PUT',
    params: {},
    data: { studentId, fullname, dob, homeAddress, career, relationshipTypeId, phoneNo },
  });

export const deleteRelationship = id =>
  request({
    prefix,
    url: `/Family/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



