import React, { Component } from 'react';
import { Row, Col, Card, Divider, message } from 'antd';
import { getCCSForRestudyToEnrollment } from 'services/classCreditSchedule';
import TableRegisterSave from './TableRegisterSave';
import TableRegisterNotSave from './TableRegisterNotSave';

class ListStudentRegisterdPage extends Component {
  state = {
    loading: false,
    searchCCSFormValue: {},
    selectedCCSRow: null,
    selectedRowKeys: [],
    ccstableData: []
  };

  handleSearchCCS = async (formValues) => {
    const { searchStudentValues } = this.props;
    if (Object.keys(searchStudentValues).length === 0) {
      return;
    }
    try {
      this.setState({ loading: true });

      const res = await getCCSForRestudyToEnrollment({
        ...formValues,
        trainTypeId: searchStudentValues.trainTypeId,
        courseId: searchStudentValues.courseId
      })

      this.setState({ loading: false, searchCCSFormValue: formValues, ccstableData: res.data.items, selectedRowKeys: [], selectedCCSRow: null });
    } catch (err) {
      message.error('C li xy ra!');
      this.setState({ loading: false });
    }
  };

  handleResetCCS = () => {
    this.setState({
      searchCCSFormValue: {},
      selectedCCSRow: null,
      selectedRowKeys: [],
      ccstableData: []
    });
  }

  handleOnSelectCCSRow = ({ selectedRowKeys, selectedRow }) => {
    this.setState({ selectedRowKeys, selectedCCSRow: selectedRow })
  }

  render () {
    const { searchStudentValues } = this.props;
    const {
      searchCCSFormValue,
      selectedCCSRow,
      loading,
      selectedRowKeys,
      ccstableData
    } = this.state;

    return (
      <Card title="Danh sch sinh vin  ng k">
        <Row gutter={24}>
          <Col span={24}>
            <TableRegisterSave
              searchStudentValues={searchStudentValues}
              handleOnSelectCCSRow={this.handleOnSelectCCSRow}
              handleSearchCCS={this.handleSearchCCS}
              loading={loading}
              selectedRowKeys={selectedRowKeys}
              selectedRow={selectedCCSRow}
              tableData={ccstableData}
            />
          </Col>
        </Row>
        <Divider>Danh sch sinh vin ng k hc li</Divider>
        <Row gutter={24}>
          <Col span={24}>
            <TableRegisterNotSave
              key={selectedCCSRow != null ? selectedCCSRow.ccsId : 'not_selected'}
              selectedCCSRow={selectedCCSRow}
              handleSaveChange={() => this.handleSearchCCS(searchCCSFormValue)}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default ListStudentRegisterdPage;



