import { request, requestDownload } from 'utils';
import qs from 'qs';

const prefix = '/api/education';

export const getClassCreditForSelect = ({
  facultyId,
  registerSessionId,
  subjectId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/DataForSelect',
    method: 'GET',
    params: {
      facultyId,
      registerSessionId,
      subjectId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getStudentInClassCredit = ({
  id,
  gradeParts,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/StudentInClassCredit',
    method: 'GET',
    params: {
      id,
      gradeParts,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
    data: {},
  });

export const getTeacherInClassCredit = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  yearEduId,
  semesterId,
  subjectId,
  isPagingEnabled = false,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/TeacherInClassCredit',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      studyFieldId,
      yearEduId,
      semesterId,
      subjectId,
      isPagingEnabled,
    },
    data: {},
  });

export const getGradingStatistics = ({
  trainTypeId,
  facultyId,
  studyFieldId,
  yearEduId,
  semesterId,
  subjectId,
  teacherId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/GetGradingStatistics',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      studyFieldId,
      yearEduId,
      semesterId,
      subjectId,
      teacherId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getCCSForRestudy = ({
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
  ...res,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/CCSForRestudy',
    method: 'GET',
    params: {
      ...res,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getCCSForRestudyToEnrollment = ({
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = false,
  ...res,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/CCSForRestudyToEnrollment',
    method: 'GET',
    params: {
      ...res,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createOrUpdateCCSForRestudy = data =>
  request({
    prefix,
    url: '/ClassCreditSchedule/CreateOrUpdateCCSForRestudy',
    data,
    method: 'POST',
  });

export const getCCSStatistics = ({
  trainTypeId,
  facultyId,
  courseId,
  yearEduId,
  semesterId,
  registerSessionId,
  isCancelled,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/GetCCSStatistics',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      yearEduId,
      semesterId,
      registerSessionId,
      isCancelled,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });

export const cancelActiveClassCredit = ({ ccsIds, isCancelled } = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/ActiveCancelCCS',
    method: 'PUT',
    params: {},
    data: { ccsIds, isCancelled }
  });

export const transferEnrollment = ({ fromCCSId, toCCSId } = {}) =>
  request({
    prefix,
    url: '/ClassCreditSchedule/TransferEnrollment',
    method: 'PUT',
    params: {},
    data: { fromCCSId, toCCSId }
  });

export const exportExcelCCSStatistics = ({
  trainTypeId,
  facultyId,
  courseId,
  yearEduId,
  semesterId,
  registerSessionId,
  isCancelled
} = {}) =>
  requestDownload({
    prefix,
    url: '/ClassCreditSchedule/ExportExcelCCSStatistics',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      yearEduId,
      semesterId,
      registerSessionId,
      isCancelled
    },
    defaultFileName: 'CCSStatisticsReport.xlsx'
  });

export const exportCCSStatistics = ({
  trainTypeId,
  facultyId,
  courseId,
  yearEduId,
  semesterId,
  registerSessionId,
  isCancelled
} = {}) =>
  requestDownload({
    prefix,
    url: '/ClassCreditSchedule/ExportCCSStatistics',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      yearEduId,
      semesterId,
      registerSessionId,
      isCancelled
    },
    defaultFileName: 'CCSStatisticsReport.pdf'
  });



