import React, { Component } from 'react';
import { Table, Row, Col, message, Modal, Select, Spin, Empty } from 'antd';
import debounce from 'lodash/debounce';
import { getCCEByCCSId, createEnrollCCEs, exportExcelCCEByCCSId, exportPdfCCEByCCSId } from 'services/classCreditEnrollment';
import { getStudents } from 'services/student';
import { BtnAddNew, BtnDelete, BtnSave, BtnExport, BtnPrint } from 'components/common/button';

const { Option } = Select;

class TableRegisterNotSave extends Component {
  constructor (props) {
    super(props);
    this.state = {
      loading: false,
      tableData: [],
      totalResults: 0,
      selectedRows: [],
      selectedRowKeys: [],
      isTableChanged: false,
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0,
        isPagingEnabled: false
      },
      showSearchStudentModal: false,
      selectedStudents: [],
      studentData: [],
      fetchingStudent: false,
    };

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Gii tnh',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Tn lp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'Tn lp hc li',
        dataIndex: 'syllableCode',
        key: 'syllableCode',
      },
      {
        title: 'n gi TC p',
        dataIndex: 'creditPrice',
        key: 'creditPrice',
      },
    ];

    this.lastFetchId = 0;
    this.getStudentByCode = debounce(this.getStudentByCode, 800);
  }

  componentDidMount () {
    const { pagination } = this.state;
    this.handleTableChange(pagination);
  }

  getStudentByCode = async (value) => {
    if (!value || value.length < 3) {
      return;
    }
    const { tableData } = this.state
    this.lastFetchId += 1;
    const fetchId = this.lastFetchId;
    this.setState({ studentData: [], fetchingStudent: true });

    try {
      const res = await getStudents({ studentCode: value });
      if (fetchId !== this.lastFetchId) {
        // for fetch callback order
        return;
      }

      const data = res.data.items.filter(x => tableData.some(s => s.studentId === x.id) === false).map(item => ({
        ...item,
        text: `${item.code}_${item.fullname}`,
        value: item.id,
      }));

      this.setState({ studentData: data, fetchingStudent: false });
    } catch (err) {
      message.error('C li xy ra!');
      this.setState({ loading: false });
    }
  };

  getTableData = async (params = {}) => {
    try {
      const { pagination } = this.state;
      this.setState({ loading: true });

      const res = await getCCEByCCSId(params);
      if (res.data && res.data.items) {
        this.setState({
          loading: false,
          tableData: res.data.items,
          totalResults: res.data.totalResults,
          pagination: {
            ...pagination,
            current: res.data.currentPage,
            total: res.data.totalResults
          }
        });
      }
    } catch (err) {
      message.error('C li xy ra!');
      this.setState({ loading: false });
    }
  }

  handleTableChange = (pagination) => {
    const { selectedCCSRow } = this.props;
    if (!selectedCCSRow || Object.keys(selectedCCSRow).length === 0) {
      return;
    }

    this.getTableData({
      cCSId: selectedCCSRow.ccsId,
      page: pagination.current,
      pageSize: pagination.pageSize,
      isPagingEnabled: pagination.isPagingEnabled || false
    });
  };

  handleDeleteRow = (e) => {
    e.preventDefault();
    const { tableData, selectedRows } = this.state;
    const totalRows = tableData.length - selectedRows.length;
    const dataRows = tableData.filter((x) => selectedRows.some((s) => s.studentId === x.studentId) === false);

    this.setState({ isTableChanged: true, tableData: dataRows, totalResults: totalRows, selectedRows: [], selectedRowKeys: [] })
  }

  handleOnChangeSelection = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys, selectedRows })
  }

  handleSaveChange = async () => {
    const { tableData } = this.state;
    const { selectedCCSRow } = this.props;

    try {
      this.setState({ loading: true });

      const studentIds = tableData.map(m => m.studentId);
      await createEnrollCCEs({
        ccsId: selectedCCSRow.ccsId,
        studentIds
      });

      message.success('Cp nht thnh cng!');
      this.setState({ loading: false, isTableChanged: false });
      if (typeof this.props.handleSaveChange === 'function') {
        this.props.handleSaveChange();
      }
    } catch (err) {
      message.error('C li xy ra!');
      this.setState({ loading: false });
    }
  }

  handleExportExcel = async () => {
    const { selectedCCSRow } = this.props;
    await exportExcelCCEByCCSId({ cCSId: selectedCCSRow.ccsId });
  }

  handleExportPdf = async () => {
    const { selectedCCSRow } = this.props;
    await exportPdfCCEByCCSId({ cCSId: selectedCCSRow.ccsId });
  }

  handleAddStudent = () => {
    const { selectedStudents, tableData } = this.state;
    const { selectedCCSRow } = this.props;

    if (selectedStudents.length === 0) {
      return;
    }
    const newStudents = selectedStudents.filter(x => tableData.some(s => s.studentId === x.id) === false).map(m => ({
      ccsId: selectedCCSRow.ccsId,
      classname: selectedCCSRow.subjectLabel,
      creditPrice: selectedCCSRow.creditPrice,
      dob: m.dob,
      fullname: m.fullname,
      genderText: m.genderText,
      studentCode: m.code,
      studentId: m.id,
      syllableCode: selectedCCSRow.syllableCode,
    }))
    this.setState({
      isTableChanged: true,
      showSearchStudentModal: false,
      selectedStudents: [],
      studentData: [],
      tableData: [...tableData, ...newStudents]
    })
  }

  handleSelectStudent = value => {
    const { selectedStudents, studentData } = this.state;
    const selectedItem = studentData.find(x => `${x.id}` === value.key);
    this.setState({
      selectedStudents: [...selectedStudents, { ...selectedItem, ...value }],
      studentData: [],
      fetchingStudent: false
    });
  };

  handleDeselectStudent = (value) => {
    const { selectedStudents } = this.state;
    const deselectedItem = selectedStudents.filter(x => `${x.id}` !== value.key);
    this.setState({
      selectedStudents: deselectedItem,
      studentData: [],
      fetchingStudent: false
    });
  };

  render () {
    const { loading, tableData, totalResults, selectedRows, selectedRowKeys, isTableChanged, showSearchStudentModal, fetchingStudent, selectedStudents, studentData } = this.state;
    const { selectedCCSRow } = this.props;

    const rowSelection = {
      selectedRowKeys,
      onChange: this.handleOnChangeSelection,
    };

    return (
      <React.Fragment>
        <Row gutter={24}>
          <Col xs={24} sm={12} md={12}>
            Tng s SV: <b>{totalResults}</b>&nbsp;
            <BtnAddNew title="Thm ng k" disabled={!selectedCCSRow || Object.keys(selectedCCSRow).length === 0} onClick={() => this.setState({ showSearchStudentModal: true })} />
            <BtnDelete title="Xa ng k" disabled={selectedRows.length === 0} onClick={this.handleDeleteRow} />
          </Col>
          <Col xs={24} sm={12} md={12} style={{ textAlign: "right" }}>
            <BtnSave disabled={!isTableChanged} onClick={this.handleSaveChange} />
            <BtnPrint title="In bo co" disabled={tableData.length === 0} onClick={this.handleExportPdf} />
            <BtnExport title="Xut Excel" disabled={tableData.length === 0} onClick={this.handleExportExcel} />
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <Table
              bordered
              loading={loading}
              rowKey="studentId"
              rowSelection={rowSelection}
              dataSource={tableData}
              columns={this.columns}
              pagination={false}
            />
          </Col>
        </Row>
        <Modal
          title="Tm kim sinh vin"
          style={{ top: 20 }}
          className="modal-search-student"
          visible={showSearchStudentModal}
          onCancel={() => this.setState({ showSearchStudentModal: false })}
          onOk={this.handleAddStudent}
          maskClosable={false}
          forceRender
        >
          <Select
            mode="multiple"
            labelInValue
            value={selectedStudents}
            placeholder="M sinh vin"
            notFoundContent={fetchingStudent ?
              <Empty
                image={<Spin />}
                imageStyle={{
                  height: 35,
                }}
                description="loading"
              /> : null
            }
            filterOption={false}
            onSearch={this.getStudentByCode}
            onSelect={this.handleSelectStudent}
            onDeselect={this.handleDeselectStudent}
          >
            {studentData.map(d => (
              <Option key={d.value}>{d.text}</Option>
            ))}
          </Select>
        </Modal>
      </React.Fragment>
    );
  }
}

export default TableRegisterNotSave;



