import React, { Component } from 'react';
import { Table, Row, Form, Col } from 'antd';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'formTableRegisterSave' })
class TableRegisterSave extends Component {
  constructor (props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'K hiu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tn mn',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'Tn lp hc li',
        dataIndex: 'syllableCode',
        key: 'syllableCode',
      },
      {
        title: 'S tn ch',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'S SV',
        dataIndex: 'noOfEnrolled',
        key: 'noOfEnrolled',
      },
      {
        title: 'n gi TC p',
        dataIndex: 'creditPrice',
        key: 'creditPrice',
      },
      {
        title: 'Tng tin',
        dataIndex: 'total',
        key: 'total',
      },
    ];
  }

  handleOnSelectChanged = (record, selected) => {
    const data = {}
    if (selected) {
      data.selectedRowKeys = [record.ccsId];
      data.selectedRow = record;
    }
    else {
      data.selectedRowKeys = [];
      data.selectedRow = null;
    }

    if (typeof this.props.handleOnSelectCCSRow === 'function') {
      this.props.handleOnSelectCCSRow(data)
    }
  }

  handleReset = () => {
    this.props.form.resetFields();
    if (typeof this.props.handleResetCCS === 'function') {
      this.props.handleResetCCS();
    }
  };

  handleSearch = (e) => {
    const { searchStudentValues } = this.props;
    if (Object.keys(searchStudentValues).length === 0) {
      return;
    }

    this.props.form.validateFields((errValidation, formValues) => {
      e.preventDefault();
      if (errValidation) {
        return;
      }

      if (typeof this.props.handleSearchCCS === 'function') {
        this.props.handleSearchCCS(formValues);
      }
    });
  };

  render () {
    const { searchStudentValues, loading, selectedRowKeys, tableData } = this.props;

    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
        initialValue: searchStudentValues.yearEduId,
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
        initialValue: searchStudentValues.semesterId,
      },
    };

    const rowSelection = {
      selectedRowKeys,
      columnTitle: ' ',
      onSelect: this.handleOnSelectChanged,
    };

    return (
      <React.Fragment>
        <Form className="form-inline-label" layout="vertical">
          <Row gutter={24}>
            <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} xs={24} sm={8} span={8} />
            <Col xs={24} sm={8} md={8}>
              <BtnSearch onClick={this.handleSearch} loading={loading} />
              <BtnClear onClick={this.handleReset} loading={loading} />
            </Col>
          </Row>
        </Form>
        <Table
          bordered
          rowKey="ccsId"
          loading={loading}
          rowSelection={rowSelection}
          dataSource={tableData}
          columns={this.columns}
          pagination={false}
        />
      </React.Fragment>
    );
  }
}

export default TableRegisterSave;



