export const getStudentGradeResult = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  testNo,
  isNoRestudy,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/GetStudentGradeResult',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      testNo,
      isNoRestudy,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

